%header-text {
  font-weight: bold;
  font-size: 12px;
  color: $second-color;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 16px;
}

.header {
  background: url("../images/header.jpg") center center/cover no-repeat;
  padding: 24px 20px 56px;
  position: relative;

  // transition: background-color 10s;

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__tel {
    @extend %header-text;
    padding: 8px 24px;
    box-sizing: content-box;

    transition: color $transition-time;

    &:hover {
      color: $main-color;
    }
  }

  &__tel-icon {
    display: none;
    width: 18px;

    img {
      width: 100%;
    }
  }
  
  &__logo {
    display: block;
    width: 115px;

    img {
      width: 100%;
    }
  }

  &__call {
    @extend %header-text;
    padding: 8px 24px;
    position: relative;

    transition: color $transition-time;

    &:hover {
      color: $main-color;
    }
    
    &::after {
      content: "";
      display: block;
      width: calc(100% - 48px);
      height: 1px;

      position: absolute;
      bottom: 0;

      background-color: $second-color;
      border-radius: 5px;
    }
  }

  &__burger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 18px;
    height: 12px;
    cursor: pointer;

    div {
      // transform-origin: left;
      width: 100%;
      height: 2px;
      background-color: $second-color;
      border-radius: 2px;

      transition: transform $transition-time;
    }
  }

  &__title {
    position: relative;
    margin-top: 314px;
    font-size: 60px;
    line-height: 1;
    font-weight: bold;
    letter-spacing: -1.5px;
    color: $second-color;

    transition: all 0.3s;

    &::before {
      content: "";
      display: block;
      width: 35px;
      height: 2px;

      position: absolute;
      top: -16px;
      background-color: $second-color;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-top: 85px;
  }

  &__link {
    @extend %header-text;
    padding: 8px 24px;

    transition: color 0.3s;

    &:hover {
      color: $main-color_light;
    }

    &--active {
      background-color: rgba($color: $second-color, $alpha: 0.1);
      border-radius: 8px;
    }
  }

  @media (max-width: 992px) {
    &__tel {
      position: absolute;
      left: 0;
      top: 452px;
      transform: translateX(-100%);
      opacity: 0;
      transition: transform $transition-time;
    }

    &__tel-icon {
      display: block;
    }

    &__logo {
      width: 73px;
    }

    &__burger {
      display: flex;
    }

    &__call {
      position: absolute;
      left: 0;
      top: 486px;
      transform: translateX(-100%);
      opacity: 0;
      transition: transform $transition-time;
    }

    &__title {
      font-size: 46px;
      line-height: 40px;
      letter-spacing: -1px;

      margin-top: 335px;
    }

    &__nav {
      position: absolute;
      top: 120px;
      left: 0;
      transform: translateX(-100%);
      opacity: 0;

      transition: transform $transition-time;
    }

    &__list {
      margin: 0;
      flex-direction: column;
    }

    &__list-item:not(:first-child) {
      margin-top: 32px;
    }

    &__link {
      font-size: 22px;
      line-height: 1;

      &--active {
        background-color: transparent;
      }
    }

    &--active {
      background: $mark-color;
      .header {
        &__tel-icon {
          visibility: hidden;
        }

        &__burger {
          height: 18px;

          div {
            &:first-child {
              transform: rotate(45deg) translate(50%, 100%);
            }

            &:nth-child(2) {
              display: none;
            }
            
            &:last-child {
              transform: rotate(-45deg) translate(50%, -100%);
            }
          }
        }

        &__tel,
        &__call,
        &__nav {
          transform: translateX(0);
          opacity: 1;
        }

        &__title {
          opacity: 0;
        }
      }
    }
  }
}
